<template>
  <div>
    <h2>我是发布话题页面</h2>
    <Panel>
      <template #header>
        <div><span>主页</span>/ <span>发布话题</span></div>
      </template>
      <template #contentwarp>
        <el-input placeholder="填写标题" v-model.lazy="title"></el-input>
        <Editor
          v-model.lazy="editcontent"
          api-key="9mieih2cqsubf30b1omff3w107ojvjfvzhmb7haq4giiy754"
          :init="{
            height: 500,
            menubar: true,
            elementpath: false,
            branding: false,
            plugins: [
              'advlist autolink lists link image charmap print preview anchor',
              'searchreplace visualblocks code fullscreen',
              'insertdatetime media table paste code help wordcount'
            ]
          }"
        />
        <button @click="getEditContent">获取文本输入的内容</button>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../layout/Panel.vue";
import Editor from "@tinymce/tinymce-vue";
export default {
  components: { Panel, Editor },
  data() {
    return {
      title: "",
      editcontent: ""
    };
  },
  methods: {
    async getEditContent() {
      console.log(typeof this.editcontent);
      console.log(typeof this.title);
      const token = sessionStorage.getItem("token");
      const { editcontent } = this;
      const { title } = this;
      console.log(token);
      // 这个函数用来提供数据给后台
      const res1 = await this.$axios.post("/topics/tab=dev", {
        accesstoken: token,
        title: title,
        tab: "dev",
        content: editcontent
      });

      console.log(res1.data);
    }
  }
};
</script>

